const API_END_POINT = "https://www.omdbapi.com";
const API_KEY = "7035c60c";

export const request = async (url) => {
  try {
    const res = await fetch(`${API_END_POINT}${url}`);

    if (res.ok) {
      return await res.json();
    }

    throw new Error("api error");
  } catch (error) {
    console.log(error.message);
  }
};

export const getMovieList = async (title, page = 1) => {
  try {
    const movieList = await request(
      `?apikey=${API_KEY}&s=${title}&page=${page}`
    );

    return movieList;
  } catch (error) {
    console.log(error.message);
  }
};

export const getMovieInfo = async (imdbID, plot = "short") => {
  try {
    const movieInfo = await request(
      `?apikey=${API_KEY}&i=${imdbID}&plot=${plot}`
    );

    return movieInfo;
  } catch (error) {
    console.log(error.message);
  }
};
