<template>
  <transition name="fade">
    <Modal :movieInfo="movieInfo" v-if="isModalOpen" :closeModal="closeModal"/>
  </transition>
 
  <input v-model="title" placeholder="enter title..." />
  <button @click="searchMovie(1)">SEARCH</button>
  
  <div class="MovieList" v-if="searchedTitle">
    <div>SEARCHED TITLE : {{searchedTitle}}</div>
    <div>CURRENT PAGE : {{curPage}}</div>
    <div class="pages">
        <button class="page-button" @click="searchMovie(1)">FIRST</button>
        <button class="page-button" @click="searchMovie(curPage - 5)">PREV</button>
        <button class="page-button" 
          v-for="pageNum in getPageList()" 
          :key="pageNum" 
          @click="searchMovie(pageNum)"
        >
          {{pageNum}} 
        </button>
        <button class="page-button" @click="searchMovie(curPage + 5)">NEXT</button>
        <button class="page-button" @click="searchMovie(totalPages)">LAST</button>
    </div>
    <div class="card-section">
        <MovieCard v-for="(card, idx) in movieList" :key="idx" :card="card" :showMovieInfo="showMovieInfo" />
    </div>
  </div>

</template>

<script>
import Modal from './components/Modal.vue'
import MovieCard from './components/MovieCard.vue'
import { getMovieList } from './utils/api'

export default {
  name: 'App',
  data() {
    return {
      title: '',
      searchedTitle: '',
      movieList: [],
      curPage: 0,
      totalPages: 0,
      isModalOpen: false,
      movieInfo: {},
    }
  },
  methods: {
    async searchMovie(page) {
      if (this.totalPages && (page < 1 || page > this.totalPages)) return;

      const res = await getMovieList(this.title, page);

      this.movieList = res.Search;
      this.searchedTitle = this.title;
      this.curPage = page || 1;
      this.totalPages = Math.ceil(res.totalResults / 10);
    },
    openModal() {
      this.isModalOpen = true;
    },
    closeModal() {
      this.isModalOpen = false;
    },
    showMovieInfo(info) {
      this.openModal();

      this.movieInfo = info;
    },
    getPageList() {
      if (this.curPage + 4 > this.totalPages) {
        return [this.totalPages - 4, this.totalPages - 3 , this.totalPages-2, this.totalPages -1, this.totalPages];
      }

      const last = Math.floor((this.curPage / 5) + 1) * 5;

      return [last - 4, last - 3 , last-2, last -1, last];
    }
  },
  components: {
    MovieCard,
    Modal,
  },
}
</script>

