  color: #2c3e50;
}


.MovieList {
    width: 100%;

    border: 1px solid skyblue;
}

.pages {
    display: flex;
    flex-direction: columns;

}

.page-button {
  border: none;
  background-color: white;
}

.card-section {
    display: flex;
    flex-wrap: wrap;
}


.fade-enter-from {
  transform: translateY(-1000px);
}
.fade-enter-active {
  transition: all 1s;
}
.fade-endter-to {
  transform: translateY(0px);
}

.fade-leave-from {
  opacity: 1;
}
.fade-leave-active {
  transition: all 1s;
}
.fade-leave-to {
  opacity: 0;
}

</style>
