<template>
    <div class="MovieCard" @click="onCardClicked">
        <img :src="card.Poster" />
        <div>{{card.Title}}  ({{card.Year}})</div>
    </div>
</template>

<script>
import { getMovieInfo } from '../utils/api'


export default {
    name: 'MovieCard',
    props: {
        card: Object,
        showMovieInfo: Function,
    },
    methods: {
        async onCardClicked() {
            const movieInfo = await getMovieInfo(this.card.imdbID);

            this.$emit('onCardClicked');

            this.showMovieInfo(movieInfo);
        }
    }
}
</script>

<style>
.MovieCard {
    margin: 10px;
}
</style>